body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: rgba(211,247,178,1);
  background: -moz-linear-gradient(left, rgba(211,247,178,1) 0%, rgba(170,245,71,1) 100%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(211,247,178,1)), color-stop(100%, rgba(170,245,71,1)));
  background: -webkit-linear-gradient(left, rgba(211,247,178,1) 0%, rgba(170,245,71,1) 100%);
  background: -o-linear-gradient(left, rgba(211,247,178,1) 0%, rgba(170,245,71,1) 100%);
  background: -ms-linear-gradient(left, rgba(211,247,178,1) 0%, rgba(170,245,71,1) 100%);
  background: linear-gradient(to right, rgba(211,247,178,1) 0%, rgba(170,245,71,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d3f7b2', endColorstr='#aaf547', GradientType=1 );
}
